import React from 'react';
import { Routes, Route } from 'react-router-dom';

//  import 
import App from './App';
import Phone from './phonedemonium/pages/PhoneMain';  //   
import Sample from './phonedemonium/index';  //   
//import Sample from './phonedemonium/sample';  //   


function Router() {
    return (
        <Routes>
            <Route path="/" element={<App />} />
            <Route path="/phone" element={<Phone />} />
            <Route path="/sample" element={<Sample />} />
        </Routes>
    );
}

export default Router;
