import React, { useState, useEffect } from 'react';
import { Heart, ArrowLeft, Trophy, Smartphone } from 'lucide-react';
import SafeImage from '../../SafeImage';


const PbtiSection = () => {
    const [currentStep, setCurrentStep] = useState('intro'); // 'intro', 'test', 'result'
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [answers, setAnswers] = useState([]);
    const [pbtiResult, setPbtiResult] = useState(null); //  

    const questions = [
        {
            id: 1,
            question: "      ?\n",
            image: "/image/phone/pbti/question1.jpg",
            options: [
                {text: ",   SNS  ", code: "S"},
                {text: " ,   ", code: "W"},
                {text: "    ", code: "B"}
            ]
        },
        {
            id: 2,
            question: " /  ?\n",
            image: "/image/phone/pbti/question2.jpg",
            options: [
                {text: ",    ", code: "V"},
                {text: "  ,   ", code: "G"},
                {text: ",    ", code: "I"}
            ]
        },
        {
            id: 3,
            question: " / . \n      ?",
            image: "/image/phone/pbti/question3.jpg",
            options: [
                {text: "/  ", code: "W"},
                {text: "      ", code: "D"},
                {text: "AI     ", code: "H,I"}
            ]
        },
        {
            id: 4,
            question: " ! \n   ?",
            image: "/image/phone/pbti/question4.jpg",
            options: [
                {text: " ,   ", code: "V"},
                {text: "  ,    ", code: "P,S"},
                {text: "    .  !", code: "G"}
            ]
        },
        {
            id: 5,
            question: "      ?\n",
            image: "/image/phone/pbti/question5.jpg",
            options: [
                {text: "    ", code: "I"},
                {text: "   ,  ", code: "W"},
                {text: "   ", code: "S"}
            ]
        },
        {
            id: 6,
            question: "   . \n     ?",
            image: "/image/phone/pbti/question6.jpg",
            options: [
                {text: "     ", code: "P"},
                {text: "     ", code: "H"},
                {text: "   ", code: "D"}
            ]
        },
        {
            id: 7,
            question: "    . \n    ?",
            image: "/image/phone/pbti/question7.jpg",
            options: [
                {text: ", ,    ", code: "P"},
                {text: "  ", code: "I"},
                {text: "  ", code: "D"}
            ]
        },
        {
            id: 8,
            question: "    10% . \n   ?",
            image: "/image/phone/pbti/question8.jpg",
            options: [
                {text: ",    ", code: "V"},
                {text: ",   SNS", code: "S"},
                {text: " ", code: "G"}
            ]
        },
        {
            id: 9,
            question: "   , \n .  ?",
            image: "/image/phone/pbti/question9.jpg",
            options: [
                {text: " ", code: "P"},
                {text: "   ", code: "V"},
                {text: "  ", code: "H"}
            ]
        },
        {
            id: 10,
            question: "  ?\n",
            image: "/image/phone/pbti/question10.jpg",
            options: [
                {text: ",     ", code: "H"},
                {text: ",     ", code: "S"},
                {text: " .    ", code: "D"}
            ]
        },
        {
            id: 11,
            question: "  . \n   ?",
            image: "/image/phone/pbti/question11.jpg",
            options: [
                {text: "   ,  ", code: "H"},
                {text: ",      ", code: "W,I"},
                {text: ",     ", code: "G,V"}
            ]
        },
        {
            id: 12,
            question: "     , \n  ?",
            image: "/image/phone/pbti/question12.jpg",
            options: [
                {text: "     ", code: "G"},
                {text: "     ", code: "W"},
                {text: "      ", code: "H"}
            ]
        }
    ];

    const handleAnswerSelect = (selectedOption) => {
        const newAnswers = [...answers, selectedOption.code];
        setAnswers(newAnswers);

        if (currentQuestion < questions.length - 1) {
            setCurrentQuestion(currentQuestion + 1);
        } else {
            setCurrentStep('result');
        }
    };

    const getResultCode = () => {
        const score = [1, 1, 1.5, 1.5, 1, 1, 1, 1, 1, 2, 2, 1];
        const phoneTypes = [
            { index: 1, code: "H", code_nm: "", score: 0 },
            { index: 2, code: "D", code_nm: "", score: 0 },
            { index: 3, code: "W", code_nm: "", score: 0 },
            { index: 4, code: "I", code_nm: "", score: 0 },
            { index: 5, code: "S", code_nm: "SNS", score: 0 },
            { index: 6, code: "P", code_nm: "", score: 0 },
            { index: 7, code: "G", code_nm: "", score: 0 },
            { index: 8, code: "V", code_nm: "", score: 0 },
        ];

        answers.forEach((codeStr, index) => {
            const weight = score[index];
            const codes = codeStr.split(',');
            codes.forEach(code => {
                const type = phoneTypes.find(t => t.code === code);
                if (type) {
                    type.score += weight;
                }
            });
        });

        phoneTypes.sort((a, b) => {
            if (b.score === a.score) return a.index - b.index;
            return b.score - a.score;
        });

        return phoneTypes[0].code;
    };

    const getResultPBTI = async (resultCode) => {
        try {
            const response = await fetch(`http://192.168.16.1:8080/phone/pbti?pcode=${encodeURIComponent(resultCode)}`);
            const result = await response.json();
            console.log(result);
            return result;
        } catch (e) {
            console.error("PBTI   :", e);
            return null;
        }
    };

    const resetTest = () => {
        setCurrentStep('intro');
        setCurrentQuestion(0);
        setAnswers([]);
        setPbtiResult(null);
    };

    // 'result'   useEffect
    useEffect(() => {
        const fetchResult = async () => {
            if (currentStep === 'result' && pbtiResult === null) {
                const resultCode = getResultCode();
                const result = await getResultPBTI(resultCode);
                setPbtiResult(result);
            }
        };
        fetchResult();
    }, [currentStep, pbtiResult]);

    //  
    if (currentStep === 'test') {
        const question = questions[currentQuestion];
        const progress = ((currentQuestion + 1) / questions.length) * 100;

        return (
            <section id="pbti" style={{
                minHeight: '100vh',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                padding: window.innerWidth <= 768 ? '15px' : '20px',
                // background: 'linear-gradient(135deg, #667eea, #764ba2)'
            }}>
                <div style={{
                    background: 'rgba(255,255,255,0.95)',
                    backdropFilter: 'blur(15px)',
                    borderRadius: '30px',
                    padding: window.innerWidth <= 768 ? '25px 20px' : '40px',
                    border: '2px solid rgba(255,255,255,0.3)',
                    maxWidth: '600px',
                    width: '100%',
                    textAlign: 'center',
                    position: 'relative',
                    boxShadow: '0 20px 40px rgba(0,0,0,0.1)'
                }}>
                    {/*   */}
                    <button
                        onClick={() => setCurrentStep('intro')}
                        style={{
                            position: 'absolute',
                            top: '15px',
                            left: '15px',
                            background: 'transparent',
                            border: 'none',
                            cursor: 'pointer',
                            color: '#666'
                        }}
                    >
                        <ArrowLeft size={window.innerWidth <= 768 ? 20 : 24}/>
                    </button>

                    {/*   */}
                    <div style={{
                        background: '#f0f0f0',
                        borderRadius: '10px',
                        height: '8px',
                        marginBottom: '25px',
                        overflow: 'hidden'
                    }}>
                        <div style={{
                            background: 'linear-gradient(45deg, #667eea, #764ba2)',
                            height: '100%',
                            width: `${progress}%`,
                            transition: 'width 0.5s ease',
                            borderRadius: '10px'
                        }}/>
                    </div>

                    <div style={{
                        fontSize: window.innerWidth <= 768 ? '14px' : '16px',
                        color: '#667eea',
                        marginBottom: '20px',
                        fontWeight: 'bold'
                    }}>
                        {currentQuestion + 1}/12
                    </div>

                    {/*    */}
                    <div style={{
                        border: '3px solid #667eea',
                        borderRadius: '25px',
                        padding: window.innerWidth <= 768 ? '20px 15px' : '30px 25px',
                        marginBottom: '25px',
                        background: 'white',
                        position: 'relative'
                    }}>
                        {/*    */}
                        <div style={{
                            position: 'absolute',
                            top: '-12px',
                            left: '50%',
                            transform: 'translateX(-50%)',
                            background: 'white',
                            padding: '0 10px'
                        }}>
                            <Heart size={window.innerWidth <= 768 ? 16 : 20} color="#667eea" fill="#667eea"/>
                        </div>

                        {/*   -   */}
                        <div style={{
                            width: window.innerWidth <= 768 ? '150px' : '250px',
                            height: window.innerWidth <= 768 ? '150px' : '250px',
                            margin: '0 auto 15px',
                            borderRadius: '15px',
                            overflow: 'hidden',
                            border: '2px solid #f0f0f0',
                            background: '#f8f9fa'
                        }}>
                            <img
                                style={{
                                    width: '100%',
                                    height: '100%',
                                    objectFit: 'cover'
                                }}
                                src={question.image}
                                alt={` ${question.id}`}
                            />
                        </div>

                        <h3 style={{
                            fontSize: window.innerWidth <= 768 ? '16px' : '18px',
                            color: '#333',
                            marginBottom: '-10px',
                            lineHeight: '1.4'
                        }}>
                            {question.question.split('\n').map((line, index) => (
                            <React.Fragment key={index}>
                                {line}
                            <br />
                            </React.Fragment>
                            ))}
                        </h3>
                    </div>

                    {/*   ( ) */}
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        gap: window.innerWidth <= 768 ? '12px' : '15px'
                    }}>
                        {question.options.map((option, index) => (
                            <button
                                key={index}
                                onClick={() => handleAnswerSelect(option)}
                                style={{
                                    background: index % 2 === 0 ?
                                        'linear-gradient(45deg, #667eea, #764ba2)' :
                                        'linear-gradient(45deg, #764ba2, #667eea)',
                                    color: 'white',
                                    border: 'none',
                                    padding: window.innerWidth <= 768 ? '15px 20px' : '18px 25px',
                                    borderRadius: '15px',
                                    fontSize: window.innerWidth <= 768 ? '14px' : '16px',
                                    fontWeight: 'bold',
                                    cursor: 'pointer',
                                    transition: 'all 0.3s ease',
                                    boxShadow: '0 4px 15px rgba(102, 126, 234, 0.3)',
                                    width: '100%'
                                }}
                                onMouseOver={(e) => {
                                    e.currentTarget.style.transform = 'scale(1.02)';
                                    e.currentTarget.style.boxShadow = '0 6px 20px rgba(102, 126, 234, 0.4)';
                                }}
                                onMouseOut={(e) => {
                                    e.currentTarget.style.transform = 'scale(1)';
                                    e.currentTarget.style.boxShadow = '0 4px 15px rgba(102, 126, 234, 0.3)';
                                }}
                            >
                                {option.text}
                            </button>
                        ))}
                    </div>
                </div>
            </section>
        );
    }
    //  
    if (currentStep === 'result') {
        if (!pbtiResult) {
            return (
                <section style={{
                    minHeight: '100vh',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    // background: 'linear-gradient(135deg, #667eea, #764ba2)',
                    color: '#fff'
                }}>
                    <p>  ...</p>
                </section>
            );
        }

        const result = pbtiResult;
        return (
            <section id="pbti" style={{
                minHeight: '100vh',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                padding: window.innerWidth <= 768 ? '15px' : '20px',
                // background: 'linear-gradient(135deg, #667eea, #764ba2)'
            }}>
                <div style={{
                    background: 'rgba(255,255,255,0.95)',
                    backdropFilter: 'blur(15px)',
                    borderRadius: '30px',
                    padding: window.innerWidth <= 768 ? '30px 25px' : '50px 40px',
                    border: '2px solid rgba(255,255,255,0.3)',
                    maxWidth: '600px',
                    width: '100%',
                    textAlign: 'center',
                    boxShadow: '0 20px 40px rgba(0,0,0,0.1)'
                }}>
                    <div style={{ fontSize: '48px', marginBottom: '-10px' }}>
                        <Trophy size={window.innerWidth <= 768 ? 40 : 48} color={result.pbti.color} />
                    </div>
                    <h2 style={{ fontSize: window.innerWidth <= 768 ? '24px' : '32px', color: result.pbti.color, marginBottom: '15px', fontWeight: 'bold' }}>
                        {result.pbti.code_nm}
                    </h2>

                    {/*   -     */}
                    <div style={{
                        width: window.innerWidth <= 768 ? '150px' : '200px',
                        height: window.innerWidth <= 768 ? '150px' : '200px',
                        margin: '30px auto',
                        borderRadius: '20px',
                        overflow: 'hidden',
                        border: `4px solid ${result.pbti.color}`,
                        boxShadow: `0 8px 25px ${result.pbti.color}40`,
                        background: 'white'
                    }}>
                        <img
                            src={`/image/phone/pbti/${result.pbti.img}`}
                            alt="PBTI  "
                            style={{
                                width: '100%',
                                height: '100%',
                                objectFit: 'cover'
                            }}
                        />
                    </div>

                    <div style={{
                        background: `${result.pbti.color}15`,
                        borderRadius: '20px',
                        padding: window.innerWidth <= 768 ? '20px' : '25px',
                        marginBottom: '25px'
                    }}>
                        <h3 style={{fontSize: window.innerWidth <= 768 ? '18px' : '20px', color: result.pbti.color, marginBottom: '15px'}}>
                              
                        </h3>
                        <div style={{
                            background: 'white',
                            padding: '15px 20px',
                            borderRadius: '10px',
                            margin: '8px 0',
                            fontSize: window.innerWidth <= 768 ? '13px' : '14px',
                            color: '#333',
                            lineHeight: '1.5'
                        }}>
                            <div
                                style={{
                                    background: 'white',
                                    padding: '15px 20px',
                                    borderRadius: '10px',
                                    margin: '8px 0',
                                    fontSize: window.innerWidth <= 768 ? '13px' : '14px',
                                    color: '#333',
                                    lineHeight: '1.5',
                                }}
                                dangerouslySetInnerHTML={{ __html: result.pbti.desc}}
                            ></div>
                        </div>
                    </div>

                    {/*    */}
                    <div style={{
                        background: `${result.pbti.color}10`,
                        borderRadius: '15px',
                        padding: window.innerWidth <= 768 ? '15px' : '20px',
                        marginBottom: '25px'
                    }}>
                        <Smartphone size={window.innerWidth <= 768 ? 20 : 24} color={result.pbti.color} style={{marginBottom: '10px'}}/>
                        <h4 style={{color: result.pbti.color, marginBottom: '15px', fontSize: window.innerWidth <= 768 ? '16px' : '18px'}}> </h4>

                        {/*   3  */}
                        <div style={{
                            display: 'flex',
                            justifyContent: 'space-around',
                            gap: window.innerWidth <= 768 ? '10px' : '15px',
                            flexWrap: 'wrap'
                        }}>
                            {result.resultModel.map(([idx, model, modelKo, img], i) => (
                                <div key={idx}>
                                    <div style={{
                                        width: window.innerWidth <= 768 ? '60px' : '80px',
                                        height: window.innerWidth <= 768 ? '60px' : '80px',
                                        margin: '0 auto 10px',
                                        borderRadius: '8px',
                                        overflow: 'hidden',
                                        background: '#f8f9fa'
                                    }}>
                                        <SafeImage
                                            src={img ? `/image/phone/model/${img}` : undefined}
                                            alt={modelKo}
                                            fallback="/image/phone/error.jpg"
                                            style={{
                                                width: '100%',
                                                height: '100%',
                                                objectFit: 'cover'
                                            }}
                                        />
                                    </div>
                                    <div style={{
                                        fontSize: window.innerWidth <= 768 ? '10px' : '12px',
                                        fontWeight: 'bold',
                                        color: '#333',
                                        textAlign: 'center'
                                    }}>
                                        {modelKo}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>

                    <button
                        onClick={resetTest}
                        style={{
                            background: `linear-gradient(45deg, ${result.pbti.color}, ${result.pbti.color}dd)`,
                            color: 'white',
                            border: 'none',
                            padding: window.innerWidth <= 768 ? '12px 25px' : '15px 30px',
                            borderRadius: '25px',
                            fontSize: window.innerWidth <= 768 ? '14px' : '16px',
                            fontWeight: 'bold',
                            cursor: 'pointer',
                            transition: 'transform 0.3s ease',
                            boxShadow: `0 5px 15px ${result.pbti.color}40`,
                            width: window.innerWidth <= 768 ? '100%' : 'auto'
                        }}
                        onMouseOver={(e) => e.currentTarget.style.transform = 'scale(1.05)'}
                        onMouseOut={(e) => e.currentTarget.style.transform = 'scale(1)'}
                    >
                         
                    </button>
                </div>
            </section>
        );
    }

    //  
    return (
        <section id="pbti" style={{
            minHeight: '100vh',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            padding: '20px',
            // background: 'linear-gradient(135deg, #667eea, #764ba2)'
        }}>
            <div style={{
                background: 'rgba(255,255,255,0.1)',
                backdropFilter: 'blur(10px)',
                borderRadius: '30px',
                padding: window.innerWidth <= 768 ? '40px 20px' : '60px 40px',
                border: '1px solid rgba(255,255,255,0.2)',
                maxWidth: '800px',
                width: '100%',
                textAlign: 'center'
            }}>
                <h2 style={{
                    fontSize: window.innerWidth <= 768 ? '24px' : '36px',
                    color: 'white',
                    marginBottom: '20px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: '15px',
                    flexWrap: 'wrap'
                }}>
                      ...  
                </h2>

                <div style={{
                    background: 'rgba(255,255,255,0.95)',
                    borderRadius: '20px',
                    padding: window.innerWidth <= 768 ? '30px 20px' : '40px',
                    margin: '30px 0',
                    color: '#333',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(255,255,255,0.3)'
                }}>
                    <h3 style={{fontSize: window.innerWidth <= 768 ? '22px' : '28px', marginBottom: '20px', color: '#333'}}>
                         PBTI 
                    </h3>
                    <img style={{width: window.innerWidth <= 768 ? '80px' : '100px'}} src="/image/phone/dr_oh.png"/>
                    <p style={{fontSize: window.innerWidth <= 768 ? '14px' : '16px', marginBottom: '30px', color: '#555', lineHeight: '1.5'}}>
                           !<br/>
                              
                    </p>

                    <div style={{
                        background: 'white',
                        borderRadius: '15px',
                        padding: window.innerWidth <= 768 ? '20px' : '30px',
                        marginBottom: '20px',
                        boxShadow: '0 4px 15px rgba(0,0,0,0.1)'
                    }}>
                        <h4 style={{fontSize: window.innerWidth <= 768 ? '16px' : '18px', marginBottom: '20px', color: '#333'}}>
                             8  !
                        </h4>

                        <div style={{
                            display: 'grid',
                            gridTemplateColumns: window.innerWidth <= 768 ? 'repeat(2, 1fr)' : 'repeat(4, 1fr)',
                            gap: window.innerWidth <= 768 ? '10px' : '15px',
                            marginBottom: '30px'
                        }}>
                            {[
                                {icon: '', text: '', color: '#ff6b6b'},
                                {icon: '', text: '', color: '#4ecdc4'},
                                {icon: '', text: '', color: '#45b7d1'},
                                {icon: '', text: '', color: '#96ceb4'},
                                {icon: '', text: 'SNS', color: '#ffeaa7'},
                                {icon: '', text: '', color: '#fd79a8'},
                                {icon: '', text: '', color: '#a29bfe'},
                                {icon: '', text: '', color: '#e17055'}
                            ].map((option, index) => (
                                <div
                                    key={index}
                                    style={{
                                        background: option.color,
                                        padding: window.innerWidth <= 768 ? '12px 8px' : '15px',
                                        borderRadius: '10px',
                                        textAlign: 'center',
                                        color: 'white',
                                        fontWeight: 'bold',
                                        transform: 'scale(1)',
                                        transition: 'transform 0.2s',
                                        display: 'flex',
                                        flexDirection: 'column',
                                        alignItems: 'center',
                                        gap: '8px',
                                        boxShadow: '0 2px 8px rgba(0,0,0,0.2)'
                                    }}
                                >
                                    <div style={{fontSize: window.innerWidth <= 768 ? '20px' : '24px'}}>{option.icon}</div>
                                    <div style={{fontSize: window.innerWidth <= 768 ? '11px' : '14px'}}>{option.text}</div>
                                </div>
                            ))}
                        </div>

                        <button
                            onClick={() => setCurrentStep('test')}
                            style={{
                                background: 'linear-gradient(45deg, #667eea, #764ba2)',
                                color: 'white',
                                border: 'none',
                                padding: window.innerWidth <= 768 ? '12px 24px' : '15px 30px',
                                borderRadius: '25px',
                                fontSize: window.innerWidth <= 768 ? '14px' : '16px',
                                fontWeight: 'bold',
                                cursor: 'pointer',
                                transition: 'transform 0.3s ease',
                                boxShadow: '0 5px 15px rgba(102, 126, 234, 0.4)',
                                width: window.innerWidth <= 768 ? '100%' : 'auto'
                            }}
                            onMouseOver={(e) => e.currentTarget.style.transform = 'scale(1.05)'}
                            onMouseOut={(e) => e.currentTarget.style.transform = 'scale(1)'}
                        >
                            PBTI  
                        </button>
                    </div>

                    <div style={{fontSize: window.innerWidth <= 768 ? '12px' : '14px', color: '#666', marginTop: '20px'}}>
                          12  
                    </div>
                </div>
            </div>
        </section>
    );
};

export default PbtiSection;